import React, { useState } from 'react';
import './Weather.css';

function Weather () {
    const [query, setQuery] = useState('');
    const [weather, setWeather] = useState({});

    const search = ( evt) => {
        if(evt.key === "Enter"){
            fetch(`${process.env.REACT_APP_WEATHER_BASE}weather?q=${query}&units=metric&appid=${process.env.REACT_APP_WEATHER_KEY}`)
            .then(res => res.json())
            .then(result => {
                setWeather(result);
                setQuery('');
                console.log(`${process.env.REACT_APP_WEATHER_BASE}weather?q=${query}&units=metric&appid=${process.env.REACT_APP_WEATHER_KEY}`);
                console.log(result);
            });
        }
    }; 

    const dateBuilder = (d) => {
        let months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
        let days = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];

        let day = days[d.getDay()];
        let date = d.getDate();
        let month = months[d.getMonth()];
        let year = d.getFullYear();

        return `${day} ${date} ${month} ${year}`;
    }

    return (
        <div id="weather" className={`weather-section ${(typeof weather.main) != "undefined" ? "i"+weather.weather[0].icon:'init'}`}>
            <main>
                <div className="search-box">
                    <input type="text" className="search-bar" placeholder="City Name eg. Vancouver"
                    onChange={e => setQuery(e.target.value)}
                    value={query}
                    onKeyPress= {search}  />
                </div>
                <div className="location-box">
                    <div className="weather-box">
                    <div className="location">
                        <h3>
                        {(typeof weather.main) != "undefined" ?(
                            `${weather.name}, ${weather.sys.country}` ) :("" )}
                            </h3>
                    </div>
                        {/* {(typeof weather.main) != "undefined" ?(
                            <div className="location">{weather.name}, {weather.sys.country} </div>
                        ) :(<div className="location">Vancouver, CA </div>)} */}

                        <div className="date"><p>
                            {(typeof weather.main) != "undefined" ?(
                            `${dateBuilder(new Date())}` ) :("" )}
                            </p>
                        </div>
                    </div>
                    {(typeof weather.main) != "undefined" ?(
                        <div className="weather-box">
                            <div className="temperature">
                                {Math.round(weather.main.temp)}℃
                            </div>
                            <div className="weather">
                                {weather.weather[0].main}
                            </div>
                        </div>
                    ) :('')}
                </div>

            </main>
        </div>
    );
}


export default Weather;