import  React, { useState} from "react";
import { useSpring } from 'react-spring';
import { motion } from "framer-motion";
import { useDrag } from "react-use-gesture";
import { COLORS } from "../../Constants";
import { Button } from "../ButtonElement";
import { DragMaskContainer,
DragMaskSection,
DragMaskColumnLeft,
DragMaskColumnRight,
DragMaskButton,
DragMaskColumnImage,
DragMaskColumnPeopleWrap,
DragMaskColumnImagePerson,
DragMaskColumnDraggable,
 } from "./DragMaskElements";
import VirusDark  from "../../images/DragMask/Virus-dark.svg";
import VirusLight  from "../../images/DragMask/Virus-light.svg";
import MiddleIni from "../../images/DragMask/middle-1.svg"
import MiddleAfter from "../../images/DragMask/middle-2.svg"
import SvgMask  from "../SvgMask";


var mediaDetect = window.matchMedia('(max-width: 760px)');



const DragMask = () => {
    const [matchMaskPos, setMatchMaskPos] = useState(false);
    const [detectMatch, setDetectMatch] = useState(false);
    const [personPos,setPersonPos] = useState({
        personLeft:0,
        personTop:0,
        personRight:0,
        personBottom:0,
    });

    const refreshComponent=() => {
        setMatchMaskPos(0);
        setDetectMatch(0);
        setPersonPos({
            personLeft:0,
            personTop:0,
            personRight:0,
            personBottom:0,
        });
    }

    const fadeLeft = {
        hidden: { opacity: 0, x: -100},
        visible: { opacity: 1, x: 0} 
    };

    const maskVariants = {
        initial: {
            opacity:0,
            y:'-100',
            x: '0',
        },
        animate: {
            opacity:1,
            y:0,
            transition:{duration:1},
        }

    };

    mediaDetect.addEventListener( "change", (e) => {
    //     if (e.matches) {
    //     console.log('This is a narrow screen — less than 760px wide.');
    //   } else {
    //     console.log('This is a wide screen — more than 760px wide.')
    //   }
      setPersonPos({
        personTop:document.getElementsByTagName("img")[5].getClientRects()[0].top,
        personLeft:document.getElementsByTagName("img")[5].getClientRects()[0].left,
        personRight:document.getElementsByTagName("img")[5].getClientRects()[0].right,
        personBottom:document.getElementsByTagName("img")[5].getClientRects()[0].bottom,

    });
      
    });


    const maskPos  = useSpring({x:0, y:0});
    const bindMaskPos = useDrag((params) => {
        const x = params.xy[0];
        const y = params.xy[1];
        maskPos.x.set(params.offset[0]);
        maskPos.y.set(params.offset[1]);
        if(personPos.personTop===personPos.personBottom) {
            // personPos.personTop=document.getElementsByTagName("img")[5].getClientRects()[0].top;
            setPersonPos({
                personTop:document.getElementsByTagName("img")[5].getClientRects()[0].top,
            });
        }
        

        if(params.dragging) {
            if(x>personPos.personLeft && x<personPos.personRight
                &&y>personPos.personTop && y<personPos.personBottom) {
                setDetectMatch(true);     
            }else {
                setDetectMatch(false);
            }
        }else {
            // console.log("---------personLeft "+ personPos.personLeft);
            // console.log("---------personRight "+ personPos.personRight);
            // console.log("---------personTop "+ personPos.personTop);
            // console.log("---------personBottom "+ personPos.personBottom);
            // console.log("x "+x+" y:"+y);
            if(x>personPos.personLeft && x<personPos.personRight
                &&y>personPos.personTop && y<personPos.personBottom) {
                    setMatchMaskPos(true); 
            }else {
                setMatchMaskPos(false); 
            }
        }
        
    });



    return (
        
        <DragMaskSection id="discover">
            <DragMaskContainer>
                <DragMaskColumnLeft>
                    <motion.p
                    initial= {{opacity: 0}}
                    animate={{opacity:1}}
                    transition={{ duration:1}}>Welcome to Project Space</motion.p>
                    <motion.h1
                    variants={fadeLeft}
                    initial='hidden'
                    animate='visible'
                    transition={{duration:1}}>Protect Yourself, Protect Your Loved Ones</motion.h1>
                    <motion.p
                    initial= {{opacity: 0}}
                    animate={{opacity:1}}
                    transition={{ duration:1}}>Help our little friend keep virus away ▷ </motion.p>
                    {/* <DragMaskButton
                    whileHover={{scale: 1.05}}
                    whileTap={{scale: 0.95,
                        backgroundColor:'#67F6E7',
                        border:'none',
                        color:'#000'}}
                    initial={{opacity: 0,
                    border:`2px solid ${COLORS.highLightText}`}}
                    animate={{opacity: 1, transition:{duration:1.5}}}
                    onClick={refreshComponent}
                    >Restart</DragMaskButton> */}
                    <Button
                    primary="true"
                    dark="true"
                    smooth={true}
                    duration={500}
                    onClick={refreshComponent}
                    >Restart</Button>
                </DragMaskColumnLeft>
                <DragMaskColumnRight>
                    <DragMaskColumnImage src={VirusDark} alt="virus"
                    style={{ display: matchMaskPos?'none':'initial'}} />
                    <DragMaskColumnImage src={VirusDark} alt="virus"
                    style={{ display: matchMaskPos?'none':'initial'}}  />
                    <DragMaskColumnImage src={VirusDark} alt="virus"
                    style={{ display: matchMaskPos?'none':'initial'}}  />
                    <DragMaskColumnImage src={VirusLight} alt="virus"
                    style={{ display: matchMaskPos?'none':'initial'}}  />
                    <DragMaskColumnImage src={VirusLight} alt="virus"
                    style={{ display: matchMaskPos?'none':'initial'}}  />

                <DragMaskColumnDraggable {...bindMaskPos()} style={{ 
                    x: maskPos.x,
                    y: maskPos.y,
                    scale:detectMatch?1.2:1,
                    display:matchMaskPos?'none':'flex',
                    }}
                    ref={el => {
                        if(!el) return;
                        console.log("mask width: " +  el.getBoundingClientRect().width); 
                      }}  >

                     <SvgMask />
                 </DragMaskColumnDraggable>
                    
                    <DragMaskColumnPeopleWrap>
                        <DragMaskColumnImagePerson src={matchMaskPos? MiddleAfter:MiddleIni} alt="person"
                        ref={el => {
                            if(!el) return;
                            personPos.personLeft=el.getBoundingClientRect().left;
                            personPos.personTop=el.getBoundingClientRect().top;
                            personPos.personRight=el.getBoundingClientRect().right;
                            personPos.personBottom=el.getBoundingClientRect().bottom;

                            // console.log("???person left: " + personPos.personLeft
                            // + " top:" +  personPos.personTop
                            // + " right: " +  personPos.personRight
                            // +" bottom: " + personPos.personBottom); 
                          }} />
                    </DragMaskColumnPeopleWrap>
                    
                </DragMaskColumnRight>
            </DragMaskContainer>
        </DragMaskSection>

    );
};

export default DragMask;